
import { defineComponent } from "vue"
import { modalController } from "@ionic/vue"
import { ICM21300141_RES, ICM21300141_REQ } from "@/shared/type/ICM21300141.type"
import { ICM21300121_RES, ICM21300121_REQ } from "@/shared/type/ICM21300121.type"
import { ICM21300112_RES, ICM21300112_REQ, ATMLangList } from "@/shared/type/ICM21300112.type"
import baseTab from "@/shared/components/Tab/BaseTab.vue"
import basePan from "@/shared/components/Tab/BaseTabPan.vue"
import phoneInput from "@/shared/components/PhoneInput/PhoneInput.vue"
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue"
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import ModifyCard from "@/shared/components/ModifyCard/ModifyCard.vue"
import { screenMode } from "@/shared/type/Approval.type"
import { APM20200113_RES } from "@/shared/type/APM20200113.type"

export default defineComponent({
    name: "icm202001sc01",
    components: {
        baseTab,
        basePan,
        phoneInput,
        SelectOption,
        BreadCrumb,
        ModifyCard
    },
    data() {
        const isInvalid = false as boolean
        const isEdit = true as boolean
        const branchCode = [] as { branchCode: string, branchName: string }[]
        const atmInfoDetail = {
            atmTel: "",
            atmLatitude: "",
            atmLongitude: "",
            list: [] as ATMLangList[],
            displayYN: "Y",
            atmFax: "",
            branchCode: "",
            atmCode: "",
            statusCode: ""
        }
        const headersIcon = [
            { key: "En", name: "common.english" },
            { key: "Km", name: "common.khmer" },
            { key: "Ko", name: "common.korean" },
            // { key: "Ja", name: "common.japanese" },
            // { key: "Zh", name: "common.chinese" },
        ]
        const atmLangDefaultValue = [
            { languageCode: "common.english", atmDesc: "", atmAddress: "", atmName: "" },
            { languageCode: "common.khmer", atmDesc: "", atmAddress: "", atmName: "" },
            { languageCode: "common.korean", atmDesc: "", atmAddress: "", atmName: "" },
            { languageCode: "common.japanese", atmDesc: "", atmAddress: "", atmName: "" },
            { languageCode: "common.chinese", atmDesc: "", atmAddress: "", atmName: "" }
        ] as ATMLangList[]

        const modifyCard = {
            registerTellerId: '',
            registerTellerName: '',
            registerDate: '',
            registerTime: '',
            updatedTellerId: '',
            updatedTellerName: '',
            updatedDate: '',
            updatedTime: '',
            deleteTellerId: '',
            deleteTellerName: '',
            deleteDate: '',
            deleteTime: '',
        }

        return {
            atmLangDefaultValue,
            headersIcon,
            modifyCard,
            atmInfoDetail,
            branchCode,
            isInvalid,
            isEdit,
            isInvalidLatitude: false,
            isInvalidLongitude: false,
            isInvalidPhoneNumber: false,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any,
        }
    },

    created() {
        this.requestBranchCode()
    },

    async mounted() {
        if (this.approvalType === '') {
            if (this.$route.query.type === "register") {
                this.isEdit = false
            } else {
                this.requestAtmDetail()
            }
        } else {
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const body = {
                        atmCode: authDetailParsed.atmCode,
                        branchCode: authDetailParsed.branchCode,
                    } as ICM21300112_REQ
                    const res = await this.$request.getRequest("ICM21300112", body, false) as ICM21300112_RES
                    await this.assignValueToTab(this.convertCommonCode(res?.body.list))
                    this.atmInfoDetail = {
                        atmLatitude: res?.body.atmLatitude,
                        atmTel: this.$format.formatPhoneNumber(res?.body.atmTel),
                        atmFax: res?.body.atmFax,
                        branchCode: res?.body.branchCode,
                        list: res?.body.list,
                        displayYN: res?.body.displayYN,
                        atmCode: res?.body.atmCode,
                        atmLongitude: res?.body.atmLongitude,
                        statusCode: res.body.statusCode
                    }
                } else {
                    await this.assignValueToTab(this.convertCommonCode(authDetailParsed.list))
                    this.atmInfoDetail = {
                        atmLatitude: authDetailParsed.atmLatitude,
                        atmTel: this.$format.formatPhoneNumber(authDetailParsed.atmTel),
                        atmFax: authDetailParsed.atmFax,
                        branchCode: authDetailParsed.branchCode,
                        list: authDetailParsed.list,
                        displayYN: authDetailParsed.displayYN,
                        atmCode: authDetailParsed.atmCode,
                        atmLongitude: authDetailParsed.atmLongitude,
                        statusCode: authDetailParsed.statusCode
                    }
                }
            })
        }
    },

    watch: {
        'atmInfoDetail.atmLatitude'(newVal) {
            if (this.$validate.validateLatitude(newVal)) {
                this.isInvalidLatitude = true
            } else {
                this.isInvalidLatitude = false
            }
        },
        'atmInfoDetail.atmLongitude'(newVal) {
            if (this.$validate.validateLongitude(newVal)) {
                this.isInvalidLongitude = true
            } else {
                this.isInvalidLongitude = false
            }
        }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },

        displayYN(): any {
            return this.$store.state.CommonCodeModule.commonCode.YnType
        },
    },

    methods: {
        async requestBranchCode() {
            const res: any = await this.$request.getRequest("COM20200113", {}, false)
            this.branchCode = res?.body.list
        },

        async assignValueToTab(atmLangList) {
            for (let i = 0; i < atmLangList.length; i++) {
                this.atmLangDefaultValue.map((item: ATMLangList) => {
                    if (item.languageCode === atmLangList[i].languageCode) {
                        item.atmDesc = atmLangList[i].atmDesc
                        item.atmAddress = atmLangList[i].atmAddress
                        item.atmName = atmLangList[i].atmName
                    }
                })
            }
        },

        async requestAtmDetail() {
            const body = {
                atmCode: this.$route.query.atmCode,
                branchCode: this.$route.query.branchCode,
            } as ICM21300112_REQ
            const res = await this.$request.getRequest("ICM21300112", body, false) as ICM21300112_RES
            await this.assignValueToTab(this.convertCommonCode(res?.body.list))
            this.atmInfoDetail = {
                atmLatitude: res?.body.atmLatitude,
                atmTel: this.$format.formatPhoneNumber(res?.body.atmTel),
                atmFax: res?.body.atmFax,
                branchCode: res?.body.branchCode,
                list: res?.body.list,
                displayYN: res?.body.displayYN,
                atmCode: res?.body.atmCode,
                atmLongitude: res?.body.atmLongitude,
                statusCode: res.body.statusCode
            }
            this.modifyCard = {
                registerTellerId: res?.body.firstRegisterTellerID,
                registerTellerName: res?.body.firstRegisterTellerName,
                registerDate: res?.body.firstRegisterDate,
                registerTime: res?.body.firstRegisterTime,
                updatedTellerId: this.atmInfoDetail.statusCode === '09' ? "" : res?.body.lastChangeTellerID,
                updatedTellerName: this.atmInfoDetail.statusCode === '09' ? "" : res?.body.lastChangeTellerName,
                updatedDate: this.atmInfoDetail.statusCode === '09' ? "" : res?.body.lastChangeDate,
                updatedTime: this.atmInfoDetail.statusCode === '09' ? "" : res?.body.lastChangeTime,
                deleteTellerId: this.atmInfoDetail.statusCode === '09' ? res?.body.lastChangeTellerID : "",
                deleteTellerName: this.atmInfoDetail.statusCode === '09' ? res?.body.lastChangeTellerName : "",
                deleteDate: this.atmInfoDetail.statusCode === '09' ? res?.body.lastChangeDate : "",
                deleteTime: this.atmInfoDetail.statusCode === '09' ? res?.body.lastChangeTime : "",
            }
        },

        async handleRegister() {
            const reqBody = {
                displayYN: this.atmInfoDetail.displayYN,
                atmCode: this.atmInfoDetail.atmCode,
                branchCode: this.atmInfoDetail.branchCode,
                atmLatitude: Number(this.atmInfoDetail.atmLatitude),
                atmLongitude: Number(this.atmInfoDetail.atmLongitude),
                atmFax: this.atmInfoDetail.atmFax,
                atmTel: this.atmInfoDetail.atmTel,
                atmLangList: this.convertLangToCode(JSON.parse(JSON.stringify(this.atmLangDefaultValue))),
            } as ICM21300121_REQ
            const res = await this.$request.getRequest("ICM21300121", reqBody, false) as ICM21300121_RES
            if (res?.header.result) {
                this.$router.push("ICM213001SC00")
                this.$dialog.onOpen({
                    content: this.$t("message.register_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        modalController.dismiss()
                    }
                })
            }
        },

        async handleEdit() {
            const reqBody = {
                displayYN: this.atmInfoDetail.displayYN,
                atmCode: this.atmInfoDetail.atmCode,
                branchCode: this.atmInfoDetail.branchCode,
                atmLatitude: Number(this.atmInfoDetail.atmLatitude),
                atmLongitude: Number(this.atmInfoDetail.atmLongitude),
                atmFax: this.atmInfoDetail.atmFax,
                atmTel: this.atmInfoDetail.atmTel,
                atmLangList: this.convertLangToCode(JSON.parse(JSON.stringify(this.atmLangDefaultValue))),
            }
            console.log(reqBody)
            const res: any = await this.$request.getRequest("ICM21300131", reqBody, false)
            if (res?.header.result) {
                this.isEdit = true
                this.$dialog.onOpen({
                    content: this.$t("message.update_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        this.requestAtmDetail()
                        modalController.dismiss()
                    }
                })
            }
        },

        async handleDelete() {
            const reqBody = {
                atmCode: this.$route.query.atmCode,
                branchCode: this.$route.query.branchCode,
            } as ICM21300141_REQ
            const res = await this.$request.getRequest("ICM21300141", reqBody, false) as ICM21300141_RES

            if (res?.header.result) {
                this.$router.push("ICM213001SC00")
                this.$dialog.onOpen({
                    content: this.$t("message.delete_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        modalController.dismiss()
                    }
                })
            }
        },

        handleViewMap() {
            window.open(`https://www.google.com/maps/search/?api=1&query=${this.atmInfoDetail.atmLatitude},${this.atmInfoDetail.atmLongitude}`)
        },

        handleOnBlur() {
            if (this.atmInfoDetail.atmTel.length < 9) {
                this.isInvalidPhoneNumber = true
            } else {
                this.isInvalidPhoneNumber = false
            }
        },

        handleOnFocus() {
            if (this.atmInfoDetail.atmTel.length < 9) {
                this.isInvalidPhoneNumber = false
            } else {
                this.isInvalidPhoneNumber = true
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case "delete":
                    if (this.$route.query.type == "edit") {
                        this.$dialog.onOpen({
                            content: this.$t("message.are_you_sure_you_want_to_delete"),
                            dialogMode: "Question",
                            callback: () => {
                                this.handleDelete().then(() =>
                                    this.$router.push("ICM213001SC00")
                                )
                            },
                        })
                    } else {
                        this.$router.push("ICM213001SC00")
                    }
                    break
                case "cancel":
                    this.$dialog.onOpen({
                        content: `${this.$t("message.cancel_without_saving_changes")}`,
                        dialogMode: "Question",
                        callback: () => {
                            if (this.$route.query.type == "edit") {
                                this.requestAtmDetail().then(() => {
                                    this.isEdit = true
                                    this.isInvalidPhoneNumber = false
                                })
                            } else {
                                this.$router.push("ICM213001SC00")
                            }
                        },
                    })
                    break
                case "save":
                    var item: any = this.atmLangDefaultValue.find((data) => data.languageCode == "common.english")
                    if (this.atmInfoDetail.atmTel.length < 9 || !item.atmName || !this.atmInfoDetail.atmCode || !this.atmInfoDetail.atmFax || !this.atmInfoDetail.atmLatitude || !this.atmInfoDetail.atmLatitude || !this.atmInfoDetail.atmTel || !this.atmInfoDetail.branchCode || this.isInvalidLatitude || this.isInvalidLongitude) {
                        this.isInvalid = true
                        this.$util.scrollToElement('inp_error')
                    } else {
                        this.$dialog.onOpen({
                            dialogMode: "Question",
                            content: this.$t("message.would_you_like_to_save"),
                            callback: () => {
                                if (this.$route.query.type == "edit") {
                                    this.handleEdit()
                                } else {
                                    this.handleRegister()
                                }
                            },
                        })
                    }

                    break
                case "edit":
                    this.isEdit = false
                    break
                default: //list case
                    this.$router.push("ICM213001SC00")
                    break
            }
        },

        convertCommonCode(listArr: ATMLangList[]): ATMLangList[] {
            const convertLangCode = listArr.map((item: ATMLangList) => {
                item.languageCode = item.languageCode ? this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode) : ''
                return item
            })
            return convertLangCode
        },

        convertLangToCode(listsArr: ATMLangList[]): ATMLangList[] {
            const data = listsArr.map((item: ATMLangList) => {
                item.languageCode = this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode, "value")
                return item
            })
            return data
        },

        handleLongitude(event: any) {
            this.$validate.keyDownLatitudeAndLongitude(event)
        },

        handleLatitude(event: any) {
            this.$validate.keyDownLatitudeAndLongitude(event)
        },

        handleFaxNo(event) {
            const BACKSPACE = 8;
            const LEFT_ARROW = 37;
            const RIGHT_ARROW = 39;
            const DELETE = 46;
            const ENTER = 13;
            const DECIMAL = 190;
            const charCode = (event.which) ? event.which : event.keyCode;
            if (
                (event.target.value.length >= 12)
                && charCode !== 8
                && (charCode !== BACKSPACE)
                && (charCode !== LEFT_ARROW)
                && (charCode !== RIGHT_ARROW)
                && (charCode !== DELETE)
                && (charCode !== ENTER)
                && (charCode !== DECIMAL)
                && (charCode !== 65 && event.ctrlKey !== true)
                && (charCode !== 67 && event.ctrlKey !== true)
                && (charCode !== 88 && event.ctrlKey !== true)
            ) {
                return event.preventDefault()
            }
        },

        checkSpace(event: Event) {
            this.$validate.validateSpace(event)
        }
    },
})
